.header {
  box-shadow: 0 0 16px rgba(0, 0, 0, 0.15);
}

.body {
  padding-top: 40px;
}

.sidebar {
  background-color: #ebf1f6;
  font-family: 'Poppins', sans-serif;
}

.sidebar__list {
  list-style: none;
  padding: 10px 20px 10px;
  display: none;
}

@media (min-width: 992px) {
  .sidebar__list {
    display: block;
  }
}

.sidebar__link {
  color: #64676a;
  font-size: 15px;
}

.footer {
  background-color: #0b0f12;
  padding-top: 90px;
  padding-bottom: 50px;
}

.footer__copyright {
  margin-top: 80px;
}

body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.body {
  flex: 1 0 auto;
}

.navbar {
  padding-top: 23px;
  padding-bottom: 23px;
}

.bg-light {
  background-color: #ffffff !important;
}

.navbar-light .navbar-nav .nav-link {
  color: #10242b;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  font-weight: 600;
  text-transform: uppercase;
}

.navbar-light .navbar-nav .active > .nav-link {
  color: #e9563d;
}

@media (min-width: 992px) {
  .navbar-brand {
    margin-right: 80px;
  }
}

.phone {
  display: flex;
  align-items: center;
}

.phone__list {
  list-style: none;
  padding-left: 10px;
  margin-bottom: 0;
}

.phone__item {
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  color: #718389;
  text-transform: uppercase;
  font-weight: 400;
  line-height: 22px;
}

.phone__item_bold {
  color: #10242b;
  font-size: 20px;
  font-weight: 500;
}

.post__img img {
  width: 100%;
  height: auto;
}

@media (min-width: 768px) {
  .post__img img {
    width: 330px;
    height: 211px;
  }
}

.post__info {
  color: #10242b;
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  font-weight: 500;
  text-transform: uppercase;
  color: #9ca5a9;
  letter-spacing: 0.9px;
}

.post__author, .post__date {
  color: #10242b;
}

.post__delimiter {
  padding: 0 15px;
}

.post__title a {
  color: #10242b;
  font-family: 'Poppins', sans-serif;
  font-size: 20px;
  font-weight: 500;
  line-height: 26px;
  letter-spacing: -0.2px;
}

.post__title a:hover {
  text-decoration: none;
  color: rgba(0, 0, 0, 0.7);
}

.post__description {
  color: #5c666a;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  line-height: 30px;
  letter-spacing: 0.15px;
}

.footer-col__title {
  color: #ffffff;
  font-family: 'Poppins', sans-serif;
  font-size: 21px;
  font-weight: 500;
  letter-spacing: -0.21px;
}

.footer-col__text {
  color: #64676a;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  line-height: 37px;
  letter-spacing: 0.15px;
  margin-bottom: 0;
}

.footer-col__contacts {
  color: #64676a;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  line-height: 37px;
  padding-left: 0;
  list-style: none;
  margin-top: 20px;
  margin-bottom: 0;
}

.footer-col__label {
  color: #c5c5c5;
  letter-spacing: 0.15px;
}

.footer-col__list {
  list-style: none;
  padding-left: 0;
  margin-bottom: 0;
}

.footer-col__link {
  color: #64676a;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  font-weight: 400;
  line-height: 37px;
}

.footer-col__link:hover {
  color: #67b930;
}

.footer-col__subscription {
  margin-top: 10px;
}

.footer-col__socials {
  margin-top: 30px;
}

.subscription {
  max-width: 290px;
}

.subscription__input {
  background-color: #252627;
  background-image: url("../img/icon_email.png");
  background-repeat: no-repeat;
  background-position: 12px;
  border: none;
  height: 52px;
  color: #ececec;
  padding-left: 40px;
  outline: none;
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
  width: 66.5%;
  font-size: 15px;
}

.subscription__input:focus {
  box-shadow: 0px 0px 10px 0px rgba(50, 50, 50, 0.7);
}

.subscription__input::placeholder {
  color: #ececec;
  font-family: 'Poppins', sans-serif;
}

.subscription__button {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
  background-color: #67b930;
  border: none;
  color: #ffffff;
  font-family: "Poppins";
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  letter-spacing: 0.35px;
  height: 52px;
  position: absolute;
  padding: 0 10px;
}

.socials {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}

.socials__icon {
  display: inline-block;
}

.copyright {
  color: #64676a;
  font-family: 'Poppins', sans-serif;
  font-size: 15px;
  text-align: center;
  border-top: 1px solid #22282c;
  padding-top: 20px;
}
