.sidebar {
    background-color: #ebf1f6;
    font-family: 'Poppins', sans-serif;

    &__list {
        list-style: none;
        padding: 10px 20px 10px;
        display: none;

        @media (min-width: $screen-lg) {
            display: block;
        }
    }

    &__link {
        color: #64676a;
        font-size: 15px;
    }
}
