.phone {
    display: flex;
    align-items: center;

    &__list {
        list-style: none;
        padding-left: 10px;
        margin-bottom: 0;
    }

    &__item {
    font-family: 'Poppins', sans-serif;
    font-size: 14px;
    color: #718389;
    text-transform: uppercase;
    font-weight: 400;
    line-height: 22px;

        &_bold {
            color: #10242b;
            font-size: 20px;
            font-weight: 500;
        }
    }
}
