.post {

    &__img {

        img {
            width: 100%;
            height: auto;

            @media (min-width: $screen-md) {
                width: 330px;
                height: 211px;
            }
        }
    }

    &__info {
        color: #10242b;
        font-family: 'Poppins', sans-serif;
        font-size: 12px;
        font-weight: 500;
        text-transform: uppercase;
        color: #9ca5a9;
        letter-spacing: 0.9px;
    }

    &__author, &__date {
        color: #10242b;
    }

    &__delimiter {
        padding: 0 15px;
    }

    &__title a {
        color: #10242b;
        font-family: 'Poppins', sans-serif;
        font-size: 20px;
        font-weight: 500;
        line-height: 26px;
        letter-spacing: -0.2px;

        &:hover {
            text-decoration: none;
            color: rgba(0,0,0,.7);
        }
    }

    &__description {
        color: #5c666a;
        font-family: 'Poppins', sans-serif;        
        font-size: 15px;
        line-height: 30px;
        letter-spacing: 0.15px;
    }
}
