.navbar {
    padding-top: 23px;
    padding-bottom: 23px;
}

.bg-light {
    background-color: #ffffff!important;
}

.navbar-light .navbar-nav .nav-link {
    color: #10242b;
    font-family: 'Poppins', sans-serif;
    font-size: 15px;
    font-weight: 600;
    text-transform: uppercase;
}

.navbar-light .navbar-nav .active>.nav-link {
    color: #e9563d;
}

.navbar-brand {
    @media (min-width: $screen-lg) {
        margin-right: 80px;
    }
}
