.footer-col {

    &__title {
        color: #ffffff;
        font-family: 'Poppins', sans-serif;
        font-size: 21px;
        font-weight: 500;
        letter-spacing: -0.21px;
    }

    &__text {
        color: #64676a;
        font-family: 'Poppins', sans-serif;
        font-size: 15px;
        line-height: 37px;
        letter-spacing: 0.15px;
        margin-bottom: 0;
    }

    &__contacts {
        color: #64676a;
        font-family: 'Poppins', sans-serif;
        font-size: 15px;
        line-height: 37px;
        padding-left: 0;
        list-style: none;
        margin-top: 20px;
        margin-bottom: 0;
    }

    &__label {
        color: #c5c5c5;
        letter-spacing: 0.15px;
    }

    &__list {
        list-style: none;
        padding-left: 0;
        margin-bottom: 0;
    }

    &__link {
        color: #64676a;
        font-family: 'Poppins', sans-serif;
        font-size: 15px;
        font-weight: 400;
        line-height: 37px;

        &:hover {
            color: #67b930;
        }
    }

    &__subscription {
        margin-top: 10px;
    }

    &__socials {
        margin-top: 30px;
    }
}
